/* for responsiveness  mobile*/
@media only screen and (max-width: 720px) {
    body {
        min-width: 360px;
    }
    .ngana_logo {
        margin-right: 200px;
        font-size: 30px;
    }
    .menu {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--blackColorHeader);
        justify-content: space-around;
        flex-direction: column;
    }
    .nav_small {
        display: inline;
    }
    .small_device {
        display: none;
    }
    .hero_section_header {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .hero_section_header div .fa {
        padding: 5px;
    }
    .main_content {
        display: flex;
        justify-content: center;
        flex-direction: column;        
    }  

    .burger_menu {
        display: block;
        font-size: 30px;
    }
    .hide_burger {
        display: none;
    }

    .hero_section {
        font-size: .8rem;
    }
    .hire_text {
        font-size: .7rem;
        min-width: 130px;
    }

    .statistic_section {
        background-color: var(--whiteColor);
        display: flex;       
        color: var(--textColorGrey);
        justify-content: center;
        align-items: center;
        padding: var(--spacing);
        border-radius: 20px;
        box-shadow: 0.1em 0.1em 0.2em;
        box-shadow: none;
        width: 100%;
        margin-left: 5px;
        margin-right: 10px;
        margin-top: -35px;
        padding: 10px
    }


    /* for main_who_items */
    .main_who_items {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: var(--textColorGreyParagraph);
        margin: 20px;
    }

    .main_who_items img {
        width: 100%;
    }

    /* for services */
    .service_content {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    /* remove link style on a tag */
    a, button {
        all: unset;
    }

    /* for portfolio */
    .portfolio_nav {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .portfolio_nav h1 {
        font-size: 1rem;
        margin: 0px;
    }

    /* for footer section */
    footer {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background: var(--blackColor);
        color: var(--whiteColor);
        padding: 10px;
    }   
}

